import React from 'react'

function Weather({city, temp, temp_min, temp_max, icon, description}) {
    return (
        <header className='data'>
            <h1 className="space">{city}</h1>
            <h5 className="space">temp: {temp} °C </h5>
            <h5 className="space">min temp:  {temp_min} °C</h5>
            <h5 className="space">max temp:  {temp_max} °C</h5>
            <img src={`http://openweathermap.org/img/wn/${icon}@2x.png`} alt={icon} width='150px' />
            <h4 className="space" >{description}</h4>
        </header>
    )
}

export default Weather
