import React, {useState} from 'react'
import Weather from './Components/Weather'
import Message from './Components/Message'
import axios from 'axios'
import './App.css';

function App() {

  const [inputText, setInputText] = useState('');
  const[text, setText] = useState(null)
  const [weatherData, setWeatherData] = useState({});

  const handleChange = (e) => {
      setInputText(e.target.value);
    }

  const handleSubmit = async (e) => {
    e.preventDefault();
    setText(true)
    try {
      const {data} =await axios.get(`http://api.openweathermap.org/data/2.5/weather?q=${inputText}&units=metric&appid=a400139a05087a74ae3d77effa4214db`)
      setWeatherData({
        city: data.name,
        temp: data.main.temp,
        temp_min:data.main.temp_min,
        temp_max:data.main.temp_max,
        feels_like: data.main.feels_like,
        icon: data.weather[0].icon,
        description: data.weather[0].description
      })
    } catch (error) {
        console.log('error:' + error)
    }
    
  }

  return (
    <div className="app">
        <form className="form" onSubmit={handleSubmit}>
          <div className="search-container">
            <input className="input-box" onChange={handleChange} type="text" id='city' placeholder="Enter City"></input>
            <button className="btn" type='submit'>search</button>
          </div>
        </form>
        
        {text ? <Weather city={weatherData.city} 
                    temp={weatherData.temp} 
                    temp_min={weatherData.temp_min} 
                    temp_max={weatherData.temp_max}
                    icon={weatherData.icon}
                    description={weatherData.description}
          /> : <Message /> }
           

    </div>
  );
}

export default App;
